@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  /* background-color: #3f3f3f; */
}

.FoodCard_grid__1empQ {
  padding: 10px;
  display: grid;
  /* grid-template-columns: 40px 40px 40px; */

  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
  align-items: stretch;
  height: 50%;
}
.FoodCard_a__8Hd1j {
  display: flex;
  flex-direction: column;
  border-radius: 13px;
  /* overflow: hidden;
  position: relative;
  cursor: pointer; */
  transition: all 300ms ease-in-out;
  padding: 30px;
}

.FoodCard_a__8Hd1j > article {
  border: 1px solid #ccc;
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
  text-align: center;
}
.FoodCard_cardFood_Price__3-f6_ {
  /* display: inline-block;
  padding: 10px;
  justify-content: space-between;
  height: 10px; */
  display: flex;
  align-items: right;
  justify-content: space-between;
  /* margin-bottom: 1em; */
  padding: 1em 1.4em;
  font-size: 1rem;
  font-weight: bold;
}

.FoodCard_a__8Hd1j > article:hover {
  transition: -webkit-transform 0.1s ease-out;
  transition: transform 0.1s ease-out;
  transition: transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.FoodCard_a__8Hd1j > article img {
  width: 100%;
  height: 25vh;
  border-top-right-radius: 20px;
  border-bottom-left-radius: 20px;
}

.FoodCard_text__3EkbE {
  padding: 0 20px 20px;
}

.FoodCard_text__3EkbE > h3 {
  text-transform: uppercase;
}

.FoodCard_text__3EkbE > button {
  background: linear-gradient(to right, #f030a7, #f0308d, #f03061);
  border-radius: 3px;
  border: 0;
  color: white;
  padding: 0;
  width: 100%;
  font-weight: 600;
  text-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
}

.FoodCard_screen__1oI-M {
  position: relative;

  background-color: #f5b342;
}
.FoodCard_innerdiv__3brWM {
  background-color: #f5b342;
  width: 70px;
  font-size: 30px;
  margin: 0;
  padding: 0;
  border: 0;
  border-top-left-radius: 10px;
  position: absolute;
  bottom: -19px;
  right: -19px;
  color: white;
}

@media (max-width: 768px) {
  .FoodCard_grid__1empQ {
    grid-template-columns: repeat(2, 1fr);
    margin: 0;
  }
}

@media (max-width: 500px) {
  .FoodCard_grid__1empQ {
    grid-template-columns: repeat(1, 1fr);
    margin: 0;
  }

  .FoodCard_a__8Hd1j > article {
    text-align: center;
  }
}

.LearnMore_learnmore__3W5it {
  position: relative;

  background-color: #f5b342;
}
.LearnMore_learnmore___1oNnp {
  background-color: #f5b342;
  width: 180px;

  font-size: 20px;
  margin: 10px;
  padding: 10px;
  border: 0;
  border-radius: 20px;
  position: absolute;
  bottom: 0;
  right: 550px;
  color: white;
}
@media (max-width: 768px) {
  .LearnMore_learnmore___1oNnp {
    position: absolute;
    right: 220px;
  }
}

@media (max-width: 500px) {
  .LearnMore_learnmore___1oNnp {
    position: absolute;
    right: 100px;
    bottom: 0px;
  }
}

